import BasicItem from "jx/editor/editionitems/BasicItem";
export default class BooleanItem extends BasicItem {
    constructor(dataObject) {
        super(Object.assign({
            icoTitle: "",
            icoSymbol: "cog",
            actions: [],
            informations: [],
            backgroundColor: "#EEEEEE"
        }, dataObject));
        //console.log(dataObject, this.dataObject)
        this.content = $(`<input type="checkbox" id="value" >`);
        this.view.find("#content").html(this.content);
        this.view.find("#value").prop('disabled', !this.editable);
        this.view.find("#value").attr("checked", (this.data.value == true || this.data.value == "true"));
        this.view.on("change", (event) => {
            if (this.autoRefresh)
                this._refreshData();
            this.onchanged.dispatch({ target: this });
        });
    }
    _refreshData() {
        this.data.value = this.value;
    }
    _refreshView() {
        this.data.value = this.value;
    }
    get value() {
        return this.view.find("#value").prop("checked");
    }
    set lock(value) {
        if (!value) {
            this.view.find("#value").prop('disabled', !this.editable);
        }
        else {
            this.view.find("#value").prop('disabled', true);
        }
    }
}
